const BASE_URL ="http://8.133.207.19:5000"

const MAX_RETRIES = 3;
const RETRY_DELAY = 2000;

const createEventSourceWithRetry = (url, handlers) => {
  const es = new EventSource(url);
  let retryCount = 0;
  let isConnected = false;

  es.onopen = () => {
    console.log('SSEè¿æ¥å·²å»ºç«‹');
    isConnected = true;
    retryCount = 0;
  };

  es.onerror = (err) => {
    if (!isConnected && retryCount < MAX_RETRIES) {
      console.log(`SSEè¿æ¥å¤±è´¥,${RETRY_DELAY/1000}ç§’åç¬¬${retryCount + 1}æ¬¡é‡è¯•...`);
      retryCount++;
      // ä¸åˆ›å»ºæ–°çš„EventSourceå®ä¾‹ï¼Œè€Œæ˜¯ç­‰å¾…ç°æœ‰å®ä¾‹è‡ªåŠ¨é‡è¿
      // æµè§ˆå™¨å†…ç½®çš„EventSourceä¼šè‡ªåŠ¨é‡è¯•è¿æ¥
    } else if (!isConnected) {
      handlers.onError?.('è¿æ¥å·²æ–­å¼€,è¯·åˆ·æ–°é¡µé¢é‡è¯•');
      es.close();
    }
  };

  return es;
};

export const createOrderStream = (eightCharacters, handlers) => {
  const url = `${BASE_URL}/api/orders/stream?eightCharacters=${encodeURIComponent(eightCharacters)}`;
  const es = createEventSourceWithRetry(url, handlers);

  es.addEventListener('stream', (e) => {
    const data = JSON.parse(e.data);
    handlers.onStream?.(data.content);
  });

  es.addEventListener('stream_end', (e) => {
    const data = JSON.parse(e.data);
    handlers.onComplete?.(data.orderId);
    es.close();
  });

  es.addEventListener('error', (e) => {
    if(e.data) {
      const data = JSON.parse(e.data);
      handlers.onError?.(data.message);
      es.close();
    }
  });

  return es;
};

export const initiatePayment = (prevOrderId, analysisType, handlers) => {
  const url = `${BASE_URL}/api/orders/${prevOrderId}/payment?type=${analysisType}`;
  const es = createEventSourceWithRetry(url, handlers);

  es.addEventListener('payment', (e) => {
    const data = JSON.parse(e.data);
    handlers.onPaymentQR?.(data.qrcodeUrl, data.orderId);
  });

  es.addEventListener('payment_success', (e) => {
    handlers.onPaymentstatus?.();
  });

  es.addEventListener('stream', (e) => {
    const data = JSON.parse(e.data);
    handlers.onAnalysisChunk?.(data.content);
  });

  es.addEventListener('stream_end', () => {
    handlers.onAnalysisEnd?.();
    es.close();
  });

  es.addEventListener('error', (e) => {
    if(e.data) {
      const data = JSON.parse(e.data);
      handlers.onError?.(data.message);
      es.close();
    }
  });

  return es;
};